.actor{
    overflow: hidden;
    .mix-list{
        ul{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 6rem;
            li{
                color: rgba($color-dark,.5);
                padding: 1rem;
                font-size: 1.2rem;
                letter-spacing: .2rem;
                transition: all .3s;
                cursor: pointer;
                font-weight: 800;
                text-transform: uppercase;
                position: relative;
                &:not(:last-child){
                    margin-right: 2rem;
                }
                @include respond(375){
                    &:not(:last-child){
                        margin-right: .7rem;
                    }
                    padding: .5rem;

                }  
    
                &::before{
                    content: '';
                    position: absolute;
                    bottom: -.3rem;
                    left: 0;
                    width: 0;
                    height: .2rem;
                    background-color: $color-dark;
                    transition: all .3s;
                
                }

                &.active{
                    color: $color-dark;
                    &::before{
                        width: 100%;
                    }
                }
            }

        }   
    }
    .mixitup-container-1,
    .mixitup-container-2{
        display: flex ;
        flex-wrap: wrap;
        justify-content: center;
        align-content: center;
        overflow: hidden;
        backface-visibility: hidden;        
        &.mix2{
            display: none;
        }
        .d-flex{
            flex-wrap: wrap;
        }
        .mycard{
            width: (100%/4);
            display: block;
            @include respond(991){
                width: (100%/3);
            }  
            @include respond(767){
                width: (100%/2);
            }  
            @include respond(425){
                width: 100%;
            }  
            @include respond(375){
                height: 44rem;
            }  
            img{
                display: block;
                width: 100%;
                backface-visibility: hidden;
            }
            position: relative;
            overflow: hidden;
            backface-visibility: hidden;
            .overlay{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba($color-dark,.7);
                transform: scale(0);
                transition: all .4s;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;

                .icon-box{
                    position: absolute;
                    top: 1.5rem;
                    right:1.5rem;
                    background-color: $maincolor-1;
                    width: 4rem;
                    height: 4rem;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    i{
                        color: #fff;
                        font-size: 2rem;
                    }
                }
                .titel-box{
                    text-align: center;
                    margin-top: auto;
                    h3{
                        color: #fff;
                        font-family: $titel-font;
                        font-size: 3rem;
                        letter-spacing: .1rem;
                        font-weight: 400;
                    }
                    p{
                        font-size: 1.2rem;
                        letter-spacing: .2rem;
                        color: #aeaeb7;
                        font-weight: 800;
                        margin-bottom: 2rem;
                    }
                }
            }
            &:hover{
                .overlay{
                    transform: scale(1);
                }
            }
        }
    }
    .newText{
        &::after,
        &::before{
            content: 'see less';
        }
    }
    
}
//start latestNew
.latestNews{
    overflow: hidden;
    .topCard{
        margin-top: 1rem;
        margin-bottom: 1rem;
        .img-box{
            width: 100%;
            img{

                width: 100%;
                height: auto;
                box-shadow: 0 .1rem 1rem rgba($color-dark,.2); 
            }
        }
        cursor: pointer;
        .text-box{
            display: flex;
            justify-content: center;
            flex-direction: column;
            text-align: left;
    
            h4{
                font-weight: 500;
                font-family: $titel-font;
                font-size: 1.7rem;
                line-height: 1.5;
                margin-bottom: 1rem;
                color: $color-dark;
                transition: all .3s;
            }
            p{
                font-weight: 400;
                font-size: 1.4rem;
                color: #777;
            }
        }
        &:hover{
            .text-box h4{
                color: $maincolor-1;
            }
        }
    }
    .news-trending{
        margin-top: 3rem;
        position: relative;
        cursor: pointer;
        width:100%;
        height: 40rem;
        display: flex;
        .img-box{
            width: 60%;
            @include respond(320){
                width: 50%;
            }  

            height: 100%;
            margin-left: auto;
            
            img{
                width: 100%;
                height: 100%;
            }
        }
        .contint{
            position: absolute;
            top: 0;
            left: 0;
            width: 40%;
            @include respond(320){
                width: 50%;
            }  

            height: 100%;
            background-color: $maincolor-1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: left;
            padding: 2.5rem;
            color: #fff;
            @include respond(425){
                padding: 2.5rem 1rem;
            }  
        i{
                margin-top: auto;
                margin-bottom: 2rem;
                font-size: 4rem;
                color: #fff;
                transition: all .4s ease-in-out;
                filter: drop-shadow( 0 0 0 rgba(#fff, 1));
                @include respond(425){
                    font-size: 3rem;
                }  
    

            }
            h3{
                font-size: 2.5rem;
                margin-bottom: 1rem;
                line-height: 1.3;
                letter-spacing: -.1rem;
                font-family: $titel-font;
                font-weight: 500;
                @include respond(425){
                    font-size: 2rem;
                }  
    
            }
            p{
                font-size: 1.4rem;
                color: #fff;
                font-weight: 400;
                margin-bottom: 2rem;

            }
        }
        .news-btn{
            position: absolute;
            top: 1rem;
            right: 1.2rem;
        }
        &:hover{
            i{
                animation: filter-heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
            }
        }
    }
    .news-Dream{
        margin-top: 4rem;
        cursor: pointer;
        img{
            width: 100%;
        }
        .contint{
            h3{
                font-size: 1.7rem;
                color: $color-dark;
                font-family: $titel-font;
                font-weight: 500;
                margin-top: 2rem;
                margin-bottom: 1.5rem;
                line-height: 1.3;
                transition: all .3s ease-in-out;
            }
            p{
                font-size: 1.4rem;
                color:#777;
                font-weight: 400;

            }

        }
        &:hover{
            h3{
                color: $maincolor-1;
            }
        }
    }
    .news-play{
        margin-top: 4rem;
        position: relative;
        .swiper-slide{
            height: auto !important;
            margin-bottom: 30px;
        }
        .swiper-container-horizontal>.swiper-pagination-bullets{
            bottom: 50%;
            left: 15rem;
            width: 100%;
            transform: translate3d(0, 0, 0) rotateZ(90deg);
            @include respond(425){
                left: 21rem;
            }  
        }
        .swiper-pagination-bullet{
            background:#fff;
            opacity: 0.5;
            width: .6rem;
            height: .6rem;
        }
        .swiper-pagination-bullet-active{
            opacity: 1;
            width: .8rem;
            height: .8rem;
        }
        img{
            width: 100%;
        }
        .contint{
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            display: flex;
            padding: 2rem;
            align-items: center;
            cursor: pointer;
            i{
                font-size: 2.5rem;
                line-height: 180%;
                padding-left: .5rem;
                color: #fff;
                border: .1rem solid #fff;
                border-radius: 50%;
                width: 5rem;
                height: 5rem;
                text-align: center;
                transition: all .4s ease-in-out;
                filter: drop-shadow( 0 0 0 rgba(#fff, 1));
                animation: filter-heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
            }
            p{
                margin-left: 1rem;
                font-size: 1.4rem;
                letter-spacing: .3rem;
                line-height: 3rem;
                color: #fff;
                font-weight: 800;
                margin-bottom: 0;

            }
    
        }
    }
    .news-motivation{
        margin-top: 3rem;
        cursor: pointer;
        @include respond(991){
            display: none;
        }  

        img{
            width: 100%;
        }
        .swiper-pagination-bullet{
            background:#fff;
            opacity: 0.5;
            width: .6rem;
            height: .6rem;
        }
        .swiper-pagination-bullet-active{
            opacity: 1;
            width: .8rem;
            height: .8rem;
        }
        .contint{
            h3{
                font-size: 1.7rem;
                color: $color-dark;
                font-family: $titel-font;
                font-weight: 500;
                margin-top: 2rem;
                margin-bottom: 1.5rem;
                line-height: 1.3;
                transition: all .3s ease-in-out;
            }
            p{
                font-size: 1.4rem;
                color:#777;
                font-weight: 400;

            }
            
        }
        &:hover{
            h3{
                color: $maincolor-1;
            }
        }

    }
}