.main-btn{
    font-size: 1.4rem;
    letter-spacing: .3rem;
    font-weight: 800;
    padding: 1.5rem 2rem;
    width: 20rem;
    text-align: center;
    border-radius: 10rem;
    display: block;
    margin:1rem  auto;
    text-transform: uppercase;
    position: relative;
    background-color:transparent;
    color: transparent;
    &:hover{
        text-decoration: none;
        color: #fff;
    }
    &::after,
    &::before{
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        transition: all .3s ease-in-out;
        font-size: 1.4rem;
        letter-spacing: .3rem;
        color: #fff;
        font-weight: 800;
        background-color: $maincolor-1;
        padding: 1.5rem 2rem;
        text-align: center;
        border-radius: 10rem;
        display: block;
        margin:4rem  auto;
        text-transform: uppercase;
        backface-visibility: hidden;
        box-shadow: 0 0 0 0 rgba($maincolor-1, 1);
    }
    &::before{
        clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
        transform:translate(0, 0.09rem);
    }
    &::after{
        clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
    }
    &:hover{
        &::before{
            transform: translate(.5rem, -.09rem);
            animation: heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
        }
        &::after{
            transform: translate(-.5rem, .09rem);
            animation: heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
        }

    }
    &:active{
        &::before{
            transform:translate(0, 0.09rem);
        }
        &::after{
            transform: translate(0, 0);
        }

    }

}
//btn white
.btn-white{
    padding:1rem 2rem;
    font-size: 1.4rem;
    font-weight: 800;
    letter-spacing: .3rem;
    text-decoration: none;
    text-transform: uppercase;
    background-color:transparent;
    color: transparent;
    backface-visibility: hidden;
    &:hover{
        text-decoration: none;
        color: transparent;
    }
    &::after,
    &::before{
        content: attr(data-text);
        backface-visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        transition: all .3s ease-in-out;
        background-color: #fff;
        color: $color-dark;
        font-size: 1rem;
        font-weight: 800;
        letter-spacing: .3rem;
        padding: 1rem 2.5rem;
        text-align: center;
        display: block;
        text-transform: uppercase;
        
    }
    &::before{
        clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
        transform:translate(0, 0.09rem);

    }
    &::after{
        clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
    }
    &:hover{
        &::before{
            transform: translate(.5rem, -.095rem);
            
        }
        &::after{
            transform: translate(-.5rem, .09rem);
        }

    }
    &:active{
        &::before{
            transform:translate(0, 0.09rem);
        }
        &::after{
            transform: translate(0, .0);
        }

    }
}