nav{
    padding: 2.5rem 0 ;
    transition: all .4s;
    @include respond(991){
        padding: 2rem 0 ;
    }

    &.scrolled{
        background-color: #fff;
        padding: 1.3rem 0;
    }
    &.nav-list-sow{
        background-color: $maincolor-1;
    }
    .nav-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        z-index: 1000;
        .nav-box{
            display: flex;
            align-items: center;
            justify-content: center;
            .toggle{
                width: 2rem;
                height: 2rem;
                cursor: pointer;
                span{
                    background-color: #000;
                    display: block;
                    width: 100%;
                    height:.2rem;
                    margin: .5rem auto;
                    backface-visibility: hidden;
                    transition: all .3s;
                }
                &.on {
                    .one {
                        transform:rotate(45deg) translate(0.6rem, 0.8rem);
                    }
                    .two {
                      opacity: 0;
                    }
                    .three {
                        transform: rotate(-45deg) translate(0.2rem, -.4rem);
                    }
                  }
            }
            .list-one{
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 1rem;
                @include respond(991){
                    display:none;
                }
                
                li{
                    margin-left: 3rem;
                    position: relative;
                    a{
                        color:transparent;
                        text-transform: uppercase;
                        padding: 1rem;
                        text-decoration: none;
                        z-index: 2;
                        font-weight: 800;
                        letter-spacing: .3rem;
                        font-size: 1.4rem;
                        &::after,
                        &::before{
                            content: attr(data-text);
                            position: absolute;
                            top: .2rem;
                            left: 1rem;
                            width: 100%;
                            transition: all .3s ease-in-out;
                            color: #1a1a1d;
                                
                        }
                        &::before{
                            clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
                        }
                        &::after{
                            clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
                        }
                        &:hover{
                            &::before{
                                transform: translate(.5rem, -.2rem);
                            }
                            &::after{
                                transform: translate(-.5rem, .2rem);
                            }
            
                        }
            
                        
                    }
                    &::before{
                        transition: all .3s linear;
                        z-index: -1;
                        content: '';
                        position: absolute;
                        bottom: .7rem;
                        right: 0;
                        background-color: $maincolor-3;
                        width: 100%;
                        margin: auto;
                        height: .5rem;
                        display: block;
                        transform: scale(0);
                        transform-origin: right ;
                    }
                    &:hover{
                        &::before{
                            transform: scale(1);
                        }
                    }

                }
            }
        }
        .logo-box{
            img{
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                @include respond(767){
                    width: 40%;
                }
            
            }
        }
        .search-box{
            form{
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: right;
                position: relative;
                button{
                    margin-top: 1rem;
                    background-color: transparent;
                    border: none;
                    outline: none;
                    margin-left: auto;
                    z-index: 2;
                    i{
                        font-size: 2rem;
                    }
                    transition: all .2s linear;
                    &:hover{
                        transform: translateY(-.2rem);
                    }
                    &:active{
                        transform: translateY(0);
                    }
                }
                input{
                    border:none;
                    z-index: 1;
                    transition: all .3s;
                    outline: none;
                    border-radius: 1rem;
                    padding: .5rem 1rem;
                    background-color: $maincolor-3;
                    width: 0;
                    opacity: 0;
                    position: absolute;
                    top: .5rem;
                    right: 0;
                }
                .search-show{
                    width: 20rem;
                    @include respond(425){
                        width: 10rem;
                    }    
    
                    opacity: 1;
                }
            }

        }
    }
}
.list-two{
    background-color: $maincolor-1;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    transition: all .4s ease-in-out;
    li{
        margin-bottom: 2rem;
        position: relative;
        text-align: center;
        a{
            text-transform: uppercase;
            padding: 1rem;
            text-decoration: none;
            z-index: 2;
            font-weight: 800;
            letter-spacing: .3rem;
            font-size: 2rem;
            color: transparent;
            &::after,
            &::before{
                content: attr(data-text);
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                transition: all .3s ease-in-out;
                color: #1a1a1d;
                
            }
            &::before{
                clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
            }
            &::after{
                clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
            }
            &:hover{
                &::before{
                    transform: translate(.5rem, -.2rem);
                }
                &::after{
                    transform: translate(-.5rem, .2rem);
                }

            }
        }
        &::before{
            transition: all .3s linear;
            z-index: -1;
            content: '';
            position: absolute;
            bottom: 1rem;
            right: 0;
            background-color: $maincolor-3;
            width: 100%;
            margin: auto;
            height: .7rem;
            display: block;
            transform: scale(0);
            transform-origin: right ;
        }
        &:hover{
            &::before{
                transform: scale(1);
            }
        }

    }
}
.list-sow{
    opacity: 1;
    visibility:visible;

}
