.main-titel{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 7rem;
    h2{
        font-family: $titel-font;
        font-size: 5rem;
        font-weight: 400;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            bottom: -2.5rem;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 40%;
            background-color: $color-dark;
            height: .2rem;
            display: block;
        }

    }
}