header{
    position: relative;
    height: 100vh;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 35%;
        height: 60%;
        background-color: $maincolor-1;
    
    }
    .main-header{
        height: 100vh;
        position: relative;
        overflow: hidden;
    
        .swiper-container {
            @include respond(tab-port){
                margin-top: 2rem ;
            }
            @include respond(425){
                margin-top: -4rem ;
            }  
            position: absolute;
            top: 56%;
            left: 50%;
            transform: translate(-50%,-50%);
            .swiper-pagination{
                bottom: 10%;
                left: 87%;
                text-align: left;
                @include respond(991){
                    left: 82%;
                }    
                @include respond(425){
                    left: 87%;
                }  
            }
            .swiper-pagination-bullet{
                outline: none;
                font-size: 1.4rem;
                width: 100%;
                margin-bottom: 1.5rem;
                height: 100%;
                display: block;
                border-radius: unset;
                background: transparent;
                opacity: 1;
                position: relative;
                color: rgba($color-dark, .5);
                transition: all .2s ;
                &::before{
                    content: '';
                    transition: all .2s ;                
                    position: absolute;
                    top: 45%;
                    left: 9rem;
                    width: 0rem;
                    height: .1rem;
                    background-color: $color-dark;
                    @include respond(425){
                       display: none;
                    }  
    
                }
                
                
            }
            .swiper-pagination-bullet-active{
                background: transparent;
                color: $color-dark;
                &::before{
                    width: 5rem;
                }
            }
        }
          
        .swiper-slide {
            width: 100% !important;
            transition-duration: 500ms !important;
            .slid-body{
                position: relative;
                .img-box{
                    position: relative;
                    img{
                        display: block;
                        margin: auto;
                        @include respond(tab-port){
                            width: 100%;
                        }
                        @include respond(425){
                            width: 100%;
                            height: 70vh;

                        }  
            
                    }
                    .titel{
                        position: absolute;
                        top: 50%;
                        left: 27.2%;
                        transform: translate(-50%,-50%);
                        @include respond(991){
                            left: 24%;
                        }  
                        @include respond(425){
                            left: 27.2%;
                        }  
                        @include respond(375){
                            left: 30.2%;
                        }  
                        @include respond(320){
                            left: 35.2%;
                        }  
            
                        h2{
                            font-size: 8.5rem;
                            letter-spacing: .2rem;
                            line-height: 7.5rem;
                            font-family:$titel-font;
                            font-weight: 400;
                            margin-bottom: 3rem;
                            position: relative;
                            @include respond(991){
                                font-size: 7rem;
                            }  
                            
                            @include respond(425){
                                font-size: 5rem;
                                line-height: 5.5rem;

                            }  
                            span{
                                display: block;
                            }
                            &::before{
                                content: '';
                                position: absolute;
                                top: 76%;
                                left: 93%;
                                transform: translate(-50%,-50%);
                                width: 4rem;
                                height: .2rem;
                                background-color: #fff;
                            }
                        }
                        p{
                            font-weight: 600;
                            line-height: 3rem;
                            font-size: 1.4rem;
                            letter-spacing: .3rem;
                            text-transform: uppercase;
                        }
                    }
                    .btn-profile{
                        position: absolute;
                        bottom: 1rem;
                        left:3.2%;
                        @include respond(tab-port){
                            bottom: 4rem;
                            left:2.2%;

                        }
                        @include respond(tab-land){
                            bottom: 5rem;
                            left:2.2%;
                        }

                        a{
                            background-color: $maincolor-1;
                            padding: 0 1rem;
                            text-decoration: none;  
                            color: #fff;
                            font-size: 1.2rem;
                            letter-spacing: .3rem;
                            text-align: center;
                            font-weight: 800;
                            line-height: 1.4rem;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            text-transform: uppercase;
                            box-shadow: 0 0 0 0 rgba($maincolor-1, 1);
                            @include respond(425){
                                font-size: 1rem;
                            }  
                            span{
                                padding:  2rem 1.2rem;
                                margin-right: -2rem;
                                height: 100%;
                                width: 30%;
                                background-color: #fff;
                                color: $color-dark;
                                margin-left: auto;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                @include respond(425){
                                    padding:   1.5rem;
                                }  
        
                                
                                i{
                                    font-size: 2rem;
                                    transition: all .3s;
                                    @include respond(425){
                                        font-size: 1.5rem;
                                    }  
    
                                }

                            }
                            &:hover{
                                animation: heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
                                i{
                                    transform: translateX(.5rem);
                                }
                            }
                        }
                    }
                }
                .icon-box{
                    position: absolute;
                    top: 14%;
                    left: 1.7rem;
                    @include respond(991){
                        display: none;
                    }

                    ul{
                        li{
                            margin-bottom: 2rem;
                            a{
                                text-decoration: none;
                                i{
                                    transition: all .3s;
                                    color: $color-dark;
                                    &:hover{
                                        color: $maincolor-1;
                                    }
                                }
                            }
                        }
                    }
                }
            }

        }
        .num-box{
            position: absolute;
            bottom: 25%;
            left: 1.7rem;
            @include respond(991){
                display: none;
            }
            p{
                transform: rotate(-90deg);
                color: $color-dark;
                margin-bottom: 0;
                &::before{
                    content: '';
                    transition: all .2s ;                
                    position: absolute;
                    top: 45%;
                    left: 5rem;
                    width: 5rem;
                    height: .1rem;
                    background-color: $color-dark;
                }
            }
        }
    }
}