.footer-contint{
    display: flex;
    flex-direction: column;
    overflow: hidden;
    .top,
    .bottom{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .top{
        margin-bottom: 2rem;
        border-bottom: .1rem solid rgba(#777,.3);
        padding: 2.5rem 0;
        .logo-box{
            width: 50%;
            @include respond(425){
                img{
                    width: 100%;
                }
            }  
        }
        .sign-box{
            width: 40%;
            @include respond(425){
                width: auto;
            }  

            display: flex;
            a{
                border-left: .1rem solid rgba(#777,.3);
                margin-left: auto;
                font-size: 1.2rem;
                letter-spacing: .3rem;
                text-transform: uppercase;
                font-weight: 700;
                color: $color-dark;
                padding: 1rem;
                padding-left: 2.5rem;
                text-decoration: none;
                transition: all .3s;
                @include respond(425){
                    font-size: 1.2rem;
                    letter-spacing: 0;
                    padding: 1rem 0 1rem 1rem;
                    padding-left: 1rem;

                }  
                @include respond(375){
                    font-size: 1rem;
                    padding: 1rem 0 1rem 1rem;

                }  
    
    
                i{
                    padding-left: 1.3rem;
                    font-size: 1.4rem;
                    @include respond(375){
                        padding-left: .5rem;
                        font-size: 1rem;
                    }  
    
                }
                &:hover ,&:hover i{
                    color: $maincolor-1;
                }
            }
        }
    }
    .bottom{
        padding: 2rem 0;
        p{
            font-size: 1.4rem;
            color: #777;
            font-weight: 400;
            @include respond(375){
                font-size: 1.3rem;
            }  

        }
        ul{
            display: flex;
            li{
                margin-left: 3rem;
                @include respond(375){
                    margin-left: 2rem;
                }  

                a{
                    text-decoration: none;
                    i{
                        transition: all .3s;
                        color: $color-dark;
                        font-size: 1.4rem;
                        &:hover{
                            color: $maincolor-1;
                        }
                    }
                }
            }
        }
    }
}