﻿html {
    // This defines what 1rem is
    font-size: 62.5%; //1 rem = 10px; 10px/16px = 62.5%

    @include respond(tab-land) { // Width > 1200px
        font-size: 56.25%; // 1rem 9px, 9/16 = 56.25%
    }
    @include respond(phone) { // Width < 900px
        font-size: 50%; // 1rem 8px, 8/16 = 50%
    }
    scroll-behavior: smooth;
}

body{
    font-size: 1.6rem;
    font-family: 'Nunito', sans-serif;
}
.overflow{
    overflow: hidden;
}
*{
    color: #1a1a1d;
    font-family: inherit;
    //border: 1px solid #f00;
}

::-webkit-scrollbar-thumb {
    background-color: $maincolor-1;

}
::-webkit-scrollbar{
    background-color: $maincolor-2;
    width: .3rem;
}
::selection {
    background: $maincolor-1;
    color: #fff;
}

button{
    cursor: pointer;
    font-size: 1.6rem;
    outline: none;
}

p, h2{
    line-height: 1.6;

    @include respond(0){
        line-height: 1.3;
    }
}

ul{
    padding: 0;
    margin: 0;
    list-style: none;
}

.svg{
    width: 1.5rem;
    height: 1.5rem;
}